.MsgListContainer {
  width: auto;
}

.MsgPreviewContainer {
  cursor: pointer;
  height: 60px;
  padding: 20px;
  align-items: center;

  background: #a6e32d18;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #a7e32d;

  transition: all linear 0.5s;
}

.MsgPreviewContainer:hover {
  background: #5a771f18;
}

.MsgPreviewContainer--Read {
  background: #5a771f18;
  color: #6c8344;
  transition: all linear 0.5s;
}
.MsgPreviewContainer--Active {
  color: white;
  transition: all linear 0.5s;
}

.SelectBox {
  display: inline-block;
  vertical-align: middle;

  width: 20px;
  height: 20px;
  border: 2px solid rgba(166, 206, 86, 0.19);
  box-sizing: border-box;
  border-radius: 2px;
}

.SelectBox:hover {
  background-color: #97b8548e;
}

.MsgTitle {
  margin-left: 10px;
  display: inline-block;
  vertical-align: middle;

  color: inherit;
  font-weight: bold;
}

.MsgSubject {
  margin-left: 20px;
  display: inline-block;
  vertical-align: middle;

  color: inherit;
}

.MsgTags {
  display: inline-block;
  vertical-align: middle;
}

.MsgDateTime {
  margin-left: 20px;
  display: inline-block;
  vertical-align: middle;

  color: inherit;
}

.MsgContent {
  font-size: large;
  max-height: none;
  min-height: 160px;
  background-color: #a6e32d0e;
  color: #acacac;

  transition: max-height 0.5s ease-in-out;
}

.MsgContent--Collapsed {
  max-height: 0;
  min-height: 0;
  overflow: hidden;
  padding: 0;

  color: #a6e32d0e;

  transition: max-height 0.5s ease-in-out;
}

.MsgContentText {
  display: inline-block;
  max-width: 85%;
  padding: 10px 10px 20px 50px;
}

.MsgContentInput {
  display: inline-block;
  width: 80%;
  padding: 10px 0px 0px 50px;
}

.MsgContentSentTo {
  color: white;
  max-width: 85%;
  padding: 10px 5px 0px 50px;
}

.MsgContentToDos {
  display: inline-block;

  padding: 10px 10px 20px 50px;
  width: 85%;
}
.MsgContentFiles {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 10px 10px 20px 50px;
  width: 85%;
}

.FilePreview {
  border-radius: 10px;
  width: 40px;
  height: 40px;
  background-color: #648532;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  transition: all 1s;
}

.FilePreview:hover {
  border-radius: 16px;
  background-color: #4a6620;
  transition: all 1s;
}

.fa-long-arrow-alt-down {
  color: #6c8344;
}

.MsgContentButtons {
  display: inline-flex;
  flex-direction: row;
  align-content: space-between;
  align-items: flex-start;
  max-width: 15%;
  padding-right: 10px;
  float: right;
}

.NewMsgBackground {
  opacity: 40%;
  background-color: #161812;
}

.fa-reply > path {
  color: #34382e;
}
.fa-forward > path {
  color: #34382e;
}

.fa-times > path {
  color: #a7e32d;
  cursor: pointer;
  float: right;
}

.NewMsgModal {
  z-index: 3;
  position: fixed;
  width: calc(100% - 320px);
  height: calc(100% - 90px);
  padding: 100px;
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: rgba(0, 0, 0, 0.4);
}

.NewMsgContainer {
  position: relative;
  padding: 20px 20px;
  width: auto;
  height: 100%;
  background-color: #363636;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.SecondLineFlex {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.CloseNewMsg {
  content: "";
  position: absolute;
  top: 10px;
  right: 15px;
  width: 20px;
  cursor: pointer;
  background: url("data:image/svg+xml;base64,")
    center / contain no-repeat;
}

.TitleNewMsg {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
  line-height: 19px;
  color: #a6ce56;
}

.InputContainer {
  margin-right: 10px;
  width: 100%;
}

.InputContainer2 {
  margin-right: 10px;
  width: 49.3%;
}

.InputContainer:last-child {
  margin-right: 0px;
}

.NewMsg {
  display: flex;
  flex-direction: column;
  height: calc(100% - 210px);
  margin-bottom: 80px;
}

.NewMsgHeadLabel {
  position: relative;
  width: 100%;
}

.NewMsgHeadText {
  position: absolute;
  left: 10px;
  top: 19px;
  color: #a6ce56;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 19px;
  color: #a6ce56;
}

.NewMsgHead {
  width: 100%;
  padding-left: 80px;
  margin-top: 9px;
}

.NewMsgForm {
  height: 100%;
}

.NewMsgTextLabel {
  position: relative;
  width: 100%;
  height: calc(100% - 240px);
  margin-top: 12px;
  flex: 1 0 auto;
}

.NewMsgBodyText {
  position: absolute;
  left: 1px;
  top: 1px;
  color: #a6ce56;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 19px;
  color: #a6ce56;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: calc(100% - 101px);
  padding-bottom: 5px;
  background-color: #262626;
}

.NewMsgBody {
  padding-top: 40px;
  height: 95%;
  resize: none;
  box-sizing: border-box;
}

.NewMsgSendOptions {
  display: flex;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.NewMsgUpload {
  width: 20px;
  margin: 10px;
  cursor: pointer;
}

.NewMsgSendButton {
  padding-right: 60px;
}

.NewMsgSendButton::after {
  content: "";
  position: absolute;
  right: 15px;
  top: 0;
  bottom: 0;
  width: 20px;
  background: url("data:image/svg+xml;base64,")
    center / contain no-repeat;
}

.css-1uccc91-singleValue {
  color: white !important;
}

.css-2613qy-menu {
  background-color: #292d22;
}
