.Header {
    height: 90px;
    width: 100%;
    background-color: #363636;
    display: inline-flex;
    overflow: hidden;
    position: fixed;
    z-index: 1;
}

.HeaderTitle{
    padding: 23px;
    margin-left: 15px;
    margin-right: 15px;
    user-select: none;
    cursor: pointer;
}

.LogoLink{
    text-decoration: none;
}

.SearchInput{
    height: 40px;
    width: 100%;
    min-width: 200px;
    max-width: 500px;
    margin-top: 25px;
    background-color: #292D22;
    border-style: none;
    padding-left: 40px;
    border-radius: 3px;
    outline: none;
}

.HeaderButton{
    margin-top: 20px;
    height: 50px;
    margin-left: 80px;
    white-space: nowrap;
    position: fixed;
    right: 130px;
    /*quitarlo*/
}

.HeaderButtonLogout{
    margin-top: 20px;
    height: 50px;
    white-space: nowrap;
    position: fixed;
    right: 55px;
    width: 40px;
    min-width: 0px !important;
    /* border-radius: 100px; */
    /*quitarlo*/
}

.LogoutIcon{
    width: 20px;
}

.HeaderSearchbar{
    position: relative;
    width: 100%;
    max-width: 500px;
    /*width: 500%*/
}

.HeaderSearchbar::before{
    content: "";
    position: absolute;
    left: 12px;
    top: 0;
    bottom: 0;
    width: 20px;
    background: url("data:image/svg+xml;base64,") center / contain no-repeat;

}
